          size: 4,
          line: {
            color: 'rgba(255, 0, 0, 0.3)',
            width: 0.5
          },
          opacity: 0.8
        },
        type: 'scatter3d',
        name: 'HT'
      };
      let nonHT = {
        x: [],
        y: [],
        z: [],
        mode: 'markers',
        marker: {
          color: 'rgba(0, 0, 255, 0.9)',
          size: 4,
          line: {
            color: 'rgba(0, 0, 255, 0.3)',
            width: 0.5
          },
          opacity: 0.8
        },
        type: 'scatter3d',
        name: 'non-HT'
      };
      // const star = 'M 10,1 L 12,7 L 18,7 L 13,11 L 15,17 L 10,13 L 5,17 L 7,11 L 2,7 L 8,7 Z';
      let theOne = {
        x: [var0],
        y: [var1],
        z: [var2],
        mode: 'markers',
        marker: {
          color: 'rgba(0, 255, 0, 0.9)',
          size: 6,
          // symbol: "star-triangle-up",
          // line: {
          //   color: 'rgba(0, 255, 0, 0.7)',
          //   width: 0.5,
          // },
          opacity: 0.9
        },
        type: 'scatter3d',
        name: 'UserInput'
      };
      for (let dPoint of this.$scatterData) {
        //let risk = this.$calRisk(dPoint.var0, dPoint.var1, dPoint.var2);
        // console.log(dPoint, risk);
        if (dPoint.label === 1) {
          HT.x.push(dPoint.var0);
          HT.y.push(dPoint.var1);
          HT.z.push(dPoint.var2);
        } else if (dPoint.label === 0) {
          nonHT.x.push(dPoint.var0);
          nonHT.y.push(dPoint.var1);
          nonHT.z.push(dPoint.var2);
        } else {
          console.log('你的label怎么回事?', dPoint.label);
        }
      }
      let scatter = document.getElementById('tester');
      Plotly.newPlot(scatter, [HT, nonHT, theOne], {
