          r: 0,
          b: 0,
          t: 0,
          pad: 0
        },
        scene: {
          xaxis: {
            title: 'x: Tmax > 6s',
            linewidth: 4
          },
          yaxis: {
            title: 'y: HIR',
            linewidth: 4
          },
          zaxis: {
            title: 'z: NIHSS score at admission',
            linewidth: 4
          }
        },
        legend: {
          // x: 1,
          // y: 1,
          orentation: 'h',
          traceorder: 'normal',
          size: 10,
          bgcolor: '#E8E8E8',
          bordercolor: '#FFFFFF',
          borderwidth: 2
        }
      });
    }
